<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data List Perkiraan</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah List Perkiraan
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="listperkiraans" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.KodeUnik">
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item.JenisPerkiraan }} </td>
                                <td class="title">{{ item.KodeUnik.slice(0,3) + '-' + item.KodeUnik.slice(3,5) + '-' + item.KodeUnik.slice(5,9)}} </td>
                                <td class="title">Rp {{ item.Debet | currency}}  </td>
                                <td class="title">Rp {{ item.Kredit | currency}} </td>
                                <td class="title">Rp {{ item.KumulatifDebet | currency}} </td>
                                <td class="title">Rp {{ item.KumulatifKredit | currency}} </td>
                                <td class="title">
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click="deleteData(item.KodeUnik)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog" persistent max-width="950" @keydown.esc="dialog = false">
            <v-card>
                <v-card-title>
                    <span class="headline">List Perkiraan</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-3">Jenis Perkiraan</h3>
                            </v-col>
                            <v-col  class="mr-2" cols="4">
                                <v-text-field dense single-line outlined label="JenisPerkiraan*" v-model="form.jenisperkiraan" required>
                                    </v-text-field>
                            </v-col>

                            <v-col cols="2">
                                <h3 class="mt-3 text-right mr-6">Kode Unik</h3>
                            </v-col>
                            <v-col class="mr-1" cols="3">
                                <v-text-field dense single-line outlined readonly label="KodeUnik*" v-model="form.kodeunik"  required>
                                </v-text-field>
                            </v-col>
                            
                            <v-col cols="2">
                                <h3 class="mt-3">Kode</h3>
                            </v-col>
                            <v-col class="mr-2" cols="2">
                                <v-text-field dense single-line outlined type="number" label="Kode*" :rules="KodeRules" :counter="3" v-model="form.kode" @keyup="NomorUnik()" required></v-text-field>
                            </v-col>
                            <v-col cols="2"> 
                            </v-col>
                             <v-col cols="2">
                                <h3 class="mt-3 text-right mr-6">Kode Daerah</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="daerahs" label="KodeDaerah*"  item-text="Nama Daerah" item-value="KodeDaerah" v-model="form.daerah" @keyup="NomorUnik()">
                                    <template slot="selection" slot-scope="data"  required>
                                        {{ data.item["KodeDaerah"] }}
                                    </template>
                                    <template slot="item" slot-scope="data">
                                        {{ data.item["KodeDaerah"] }} - {{ data.item["Nama Daerah"] }}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-3">Debet</h3>
                            </v-col>
                            <v-col class="mr-2" cols="3">
                                <v-text-field dense single-line outlined label="Debet*" v-model="form.debet"  required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-3">Kredit</h3>
                            </v-col>                            
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Kredit*" v-model="form.kredit"  required>
                                </v-text-field>
                            </v-col>

                            <!-- <v-autocomplete :items="daerahs" item-text="Nama Daerah" item-value="KodeDaerah" 
                                label="KodeDaerah*" v-model="form.daerah" @keyup="NomorUnik()" required></v-autocomplete>  -->
                            
                           

                            <!-- <v-col cols="2">
                                <h3 class="mt-3">Nomor Kode</h3>
                            </v-col>
                            <v-col class="mr-3" cols="4">
                                <v-text-field dense single-line outlined label="NomorKode*" type="number" :rules="NomorRules" :counter="4" v-model="form.nomorkode" @keyup="NomorUnik()" required>
                                </v-text-field>
                            </v-col> -->

                            <!-- <v-col cols="2">
                                <h3 class="mt-3 mr-6">Saldo</h3>
                            </v-col>                            
                            <v-col class="mr-4" cols="3">
                                <v-text-field dense single-line outlined label="Saldo*" v-model="form.saldo"  required>
                                </v-text-field>
                            </v-col> -->
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
export default {
    data() {
        return {
            statusUpdate: true,
            sortBy: 'Jenis Perkiraan',
            sortDirection: 'ASC',
            dialog: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                    class: 'title'
                },
                {
                    text: 'Jenis Perkiraan',
                    value: 'JenisPerkiraan',
                    class: 'title'
                },
                {
                    text: 'Kode Perkiraan',
                    value: 'KodeUnik',
                    class: 'title'
                },
                {
                    text: 'Debet',
                    value: 'Debet',
                    class: 'title'
                },
                {
                    text: 'Kredit',
                    value: 'Kredit',
                    class: 'title'
                },
                {
                    text: 'Nilai Kumulatif Debet',
                    value: 'Kumulatif',
                    class: 'title'
                },
                {
                    text: 'Nilai Kumulatif Kredit',
                    value: 'Kumulatif',
                    class: 'title'
                },
                {
                    text: 'Aksi',
                    value: null,
                    class: 'title'
                },
            ],
            daerahs: [],
            listperkiraans: [],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            form: {
                jenisperkiraan: '',
                kode: '',
                daerah: '',
                nomorkode: '',
                kodeunik:'',
                debet: 0,
                kredit: 0,
                kumulatifdebet: 0,
                kumulatifkredit: 0,
            },
            KodeRules: [
                    value => (value || '').length == 3 || 'Kode HARUS 3 Karakter',
                ],

            listperkiraan: new FormData,
            typeInput: 'new',
            errors: '',
            updatedId: '',
        }
    },
    methods: {
        getDaerah(){
            var uri = this.$apiUrl + 'daerah'
            this.$http.get(uri).then(response => {
                this.daerahs = response.data
            })
        },
        getData() {
            var uri = this.$apiUrl + 'listperkiraan'
            this.$http.get(uri).then(response => {
                this.listperkiraans = response.data
            })
        },
        // Activity(key) {
        //     // untuk memposting aktivitas user yang login
        //     var activity = new FormData
        //     activity.append('activity', key)
        //     var uri = this.$apiUrl + 'activity'
        //     this.$http.post(uri, activity).then(response => {}).catch(error => {
        //         this.errors = error
        //     })
        // },
        sendData() {
        this.listperkiraan.append('jenisperkiraan', this.form.jenisperkiraan);
        this.listperkiraan.append('kodeunik', this.form.kodeunik);
        this.listperkiraan.append('debet', this.form.debet);
        this.listperkiraan.append('kredit', this.form.kredit);
        this.listperkiraan.append('kumulatifdebet', this.form.kumulatifdebet);        
        this.listperkiraan.append('kumulatifkredit', this.form.kumulatifkredit);        
        var uri = this.$apiUrl + 'listperkiraan'
        this.load = true
        this.$http.post(uri, this.listperkiraan).then(response => {
            this.snackbar = true; //mengaktifkan snackbar
            this.color = 'green'; //memberi warna snackbar
            this.text = response.data; //memasukkan pesan ke snackbar
            this.load = false;
            this.dialog = false
            this.getData(); //mengambil data user
            this.resetForm();
//            this.Activity('Melakukan Create List Perkiraan');
        }).catch(error => {
            this.errors = error
            this.snackbar = true;
            this.text = 'Try Again';
            this.color = 'red';
            this.load = false;
        })
      },
        updateData() {
            this.listperkiraan.append('jenisperkiraan', this.form.jenisperkiraan);
            this.listperkiraan.append('kodeunik', this.form.kodeunik);
            this.listperkiraan.append('debet', this.form.debet);
            this.listperkiraan.append('kredit', this.form.kredit);
            this.listperkiraan.append('kumulatifdebet', this.form.kumulatifdebet);
            this.listperkiraan.append('kumulatifkredit', this.form.kumulatifkredit);
            var uri = this.$apiUrl + 'listperkiraan/' + this.updatedId;
            this.load = true
            this.$http.post(uri, this.listperkiraan).then(response => {
                this.snackbar = true; //mengaktifkan snackbar
                this.color = 'green'; //memberi warna snackbar
                this.text = response.data; //memasukkan pesan ke snackba
                this.load = false;
                this.dialog = false
                this.statusUpdate = true
                this.getData(); //mengambil data user
                this.resetForm();
                // this.Activity('Melakukan Update List Perkiraan');
                this.typeInput = 'new';
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
                this.load = false;
                this.typeInput = 'new';
            })
        },
        editHandler(item) {
            this.typeInput = 'edit';
            this.statusUpdate = false;
            this.dialog = true;
            this.form.jenisperkiraan = item.JenisPerkiraan;
            this.form.kodeunik = item.KodeUnik;
            this.form.debet = item.Debet;
            this.form.kredit = item.Kredit;
            this.form.kode = item.KodeUnik.slice(0,3);
            this.form.daerah = item.KodeUnik.slice(3,5);
            this.form.nomorkode = item.KodeUnik.slice(5,9);
            this.updatedId = item['KodeUnik'];
        },
        NomorUnik() {
            this.form.kodeunik = this.form.kode + this.form.daerah
        },
        deleteData(deleteId) { //menghapus data
            var uri = this.$apiUrl +
                'listperkiraan/' + deleteId; //data dihapus berdasarkan id
            this.$http.delete(uri).then(response => {
                this.snackbar = true;
                this.text = response.data;
                this.color = 'green'
                this.deleteDialog = false;
                this.getData();
                // this.Activity('Melakukan Delete List Perkiraan');
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
            })
        },
        setForm() {
            if (this.typeInput === 'new') {
                this.sendData()
            } else {
                this.updateData()
            }
        },
        close() {
            this.dialog = false
            this.resetForm()
        },
        resetForm() {
            this.form = {
                jenisperkiraan: '',
                kode: '',
                daerah: '',
                nomorkode: '',
                kodeunik: '',
            }
            this.statusUpdate = true
        }
    },
    mounted() {
        this.getData();
        this.getDaerah();
    },
    
}
</script>